import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ArtShopComponent } from './art-shop.component';

const routes: Routes = [{
    path: '',
    children: [
        {
            path: '',
            component: ArtShopComponent
        }
    ]
}];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class ArtShopRoutingModule { }
